/* src/components/ProductModal/ProductModal.css */

.product-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1002;
  padding: 1rem;
  animation: fadeIn 0.3s ease;
}

.product-modal {
  background: white;
  border-radius: 16px;
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.2);
  animation: slideUp 0.3s ease;
}

.product-modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: white;
  border: none;
  padding: 0.5rem;
  cursor: pointer;
  color: #6b7280;
  transition: all 0.2s;
  border-radius: 8px;
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.product-modal-close:hover {
  background: #f3f4f6;
  color: #111827;
}

.product-modal-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}

.product-modal-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-modal-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f9fafb;
  color: #d1d5db;
}

.product-modal-info {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.product-modal-title {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 1rem;
}

.product-modal-description {
  font-size: 1rem;
  color: #4b5563;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.product-modal-price {
  display: flex;
  align-items: baseline;
  gap: 1rem;
  margin-bottom: 2rem;
}

.price-regular {
  font-size: 1.25rem;
  color: #9ca3af;
  text-decoration: line-through;
}

.price-sale {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
}

.product-modal-actions {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-top: auto;
}

.quantity-selector {
  display: flex;
  align-items: center;
  gap: 1rem;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  padding: 0.25rem;
}

/* Responsive */
@media (max-width: 768px) {
  .product-modal {
    max-width: 100%;
    max-height: 100%;
    height: 100%;
    border-radius: 0;
  }

  .product-modal-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
  }

  .product-modal-image {
    max-height: 300px;
  }

  .product-modal-info {
    padding: 1.5rem;
  }

  .product-modal-title {
    font-size: 1.5rem;
  }

  .product-modal-actions {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
  }

  .quantity-selector {
    width: 100%;
    justify-content: center;
  }
}